<template>
	<view class="cart">
		<view class="top">
			<view class="radio">
				<radio :checked="selectAll" @click="selectAllButton" />
			</view>
		</view>
		<view class="cart-list">
			<CartItem v-for="(i,index) in 10" :key="index" ref="cartItem" @changePrice="changePrice"></CartItem>
		</view>
		<view class="bottom">
			总金额:
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				selectAll: false,
			}
		},
		methods: {
			// 全选按钮
			selectAllButton() {
				this.selectAll = this.selectAll ? false : true;
				console.log(this.$refs);
				if (this.selectAll) {
					this.$refs.cartItem.forEach(item => {
						item.productInfo.isSelect = 1;
					})
				} else {
					this.$refs.cartItem.forEach(item => {
						item.productInfo.isSelect = 0;
					})
				}
			},
			// 改变商品总金额
			changePrice(e){
				console.log(e);
			}
		}

	}
</script>

<style lang="scss">
.cart{
	padding-bottom: 100rpx;	
	.top{
		position: fixed;
		top: var(--window-top);
		z-index: 100;
	}
	.bottom{
		position: fixed;
		z-index: 100;
		width: 100vw;
		background: #fff;
		right: 0;
		bottom: var(--window-bottom);
		height: 100rpx;
	}
}
</style>